"use client";

import { useParams } from "next/navigation";
import Link from "next/link";
import Navbar from "@/components/Navbar";
import Footer from "@/components/Footer";
import { getEpisodeById, episodes } from "@/data/episodes";
import { useState } from "react";

export default function EpisodeDetailPage() {
  const params = useParams();
  const id = params.id as string;
  const episode = getEpisodeById(id);
  const [isVideoLoaded, setIsVideoLoaded] = useState(false);

  if (!episode) {
    return (
      <div className="min-h-screen flex flex-col">
        <Navbar />
        <div className="flex-1 flex items-center justify-center bg-gray-50">
          <div className="text-center">
            <div className="w-24 h-24 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-6">
              <svg className="w-12 h-12 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
              </svg>
            </div>
            <h1 className="text-3xl font-bold text-gray-900 mb-4">Episode Not Found</h1>
            <p className="text-gray-600 mb-8">
              The episode you're looking for doesn't exist or has been removed.
            </p>
            <Link 
              href="/episodes"
              className="bg-gradient-to-r from-purple-600 to-blue-600 text-white px-6 py-3 rounded-full font-semibold hover:from-purple-700 hover:to-blue-700 transition-all duration-200"
            >
              Back to Episodes
            </Link>
          </div>
        </div>
        <Footer />
      </div>
    );
  }

  // Get related episodes (same genre, excluding current episode)
  const relatedEpisodes = episodes
    .filter(ep => ep.genre === episode.genre && ep.id !== episode.id)
    .slice(0, 3);

  return (
    <div className="min-h-screen flex flex-col">
      <Navbar />
      
      {/* Episode Header */}
      <section className="bg-gradient-to-r from-purple-600 to-blue-600 text-white py-8">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center space-x-4 mb-4">
            <Link 
              href="/episodes"
              className="flex items-center text-white/80 hover:text-white transition-colors duration-200"
            >
              <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
              Back to Episodes
            </Link>
          </div>
          <div className="flex flex-col md:flex-row items-start md:items-center justify-between">
            <div>
              <h1 className="text-3xl md:text-4xl font-bold mb-2">{episode.title}</h1>
              <div className="flex items-center space-x-4 text-white/90">
                <span className="bg-white/20 px-3 py-1 rounded-full text-sm font-medium">
                  {episode.genre}
                </span>
                <span>{episode.duration}</span>
                <div className="flex items-center">
                  <svg className="w-4 h-4 text-yellow-400 mr-1" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"/>
                  </svg>
                  <span>{episode.rating}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Video Player Section */}
      <section className="py-8 bg-black">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="video-container">
            {!isVideoLoaded && (
              <div className="w-full h-96 bg-gray-900 rounded-lg flex items-center justify-center">
                <div className="text-center text-white">
                  <div className="w-16 h-16 bg-white/10 rounded-full flex items-center justify-center mx-auto mb-4">
                    <svg className="w-8 h-8" fill="currentColor" viewBox="0 0 24 24">
                      <path d="M8 5v14l11-7z"/>
                    </svg>
                  </div>
                  <p>Loading video player...</p>
                </div>
              </div>
            )}
            <video 
              controls 
              preload="metadata"
              onLoadedData={() => setIsVideoLoaded(true)}
              className={isVideoLoaded ? "block" : "hidden"}
              poster={episode.thumbnail}
            >
              <source src={episode.videoUrl} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
          </div>
        </div>
      </section>

      {/* Episode Details */}
      <section className="py-12 bg-white">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Main Content */}
            <div className="lg:col-span-2">
              <h2 className="text-2xl font-bold text-gray-900 mb-4">About This Episode</h2>
              <p className="text-gray-700 text-lg leading-relaxed mb-6">
                {episode.description}
              </p>

              {/* Episode Info */}
              <div className="bg-gray-50 rounded-lg p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Episode Information</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <span className="text-gray-600 font-medium">Season:</span>
                    <span className="ml-2 text-gray-900">{episode.season}</span>
                  </div>
                  <div>
                    <span className="text-gray-600 font-medium">Episode:</span>
                    <span className="ml-2 text-gray-900">{episode.episode}</span>
                  </div>
                  <div>
                    <span className="text-gray-600 font-medium">Release Date:</span>
                    <span className="ml-2 text-gray-900">
                      {new Date(episode.releaseDate).toLocaleDateString()}
                    </span>
                  </div>
                  <div>
                    <span className="text-gray-600 font-medium">Director:</span>
                    <span className="ml-2 text-gray-900">{episode.director}</span>
                  </div>
                </div>
              </div>

              {/* Cast */}
              <div className="mt-8">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Cast</h3>
                <div className="flex flex-wrap gap-2">
                  {episode.cast.map((actor, index) => (
                    <span 
                      key={index}
                      className="bg-purple-100 text-purple-800 px-3 py-1 rounded-full text-sm font-medium"
                    >
                      {actor}
                    </span>
                  ))}
                </div>
              </div>
            </div>

            {/* Sidebar */}
            <div className="lg:col-span-1">
              {/* Episode Thumbnail */}
              <div className="mb-8">
                <img 
                  src={episode.thumbnail}
                  alt={`Thumbnail for ${episode.title}`}
                  className="w-full rounded-lg shadow-lg"
                  onError={(e) => { 
                    const target = e.target as HTMLImageElement;
                    target.src = "https://placehold.co/400x300?text=Episode+Thumbnail+Unavailable";
                  }}
                />
              </div>

              {/* Download Options */}
              <div className="bg-gray-50 rounded-lg p-6 mb-8">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Download Options</h3>
                <div className="space-y-3">
                  <a 
                    href={episode.videoUrl}
                    download
                    className="block w-full bg-gradient-to-r from-purple-600 to-blue-600 text-white py-3 px-4 rounded-lg font-semibold text-center hover:from-purple-700 hover:to-blue-700 transition-all duration-200"
                  >
                    Download HD (720p)
                  </a>
                  <a 
                    href={episode.videoUrl}
                    download
                    className="block w-full bg-gray-200 text-gray-800 py-3 px-4 rounded-lg font-semibold text-center hover:bg-gray-300 transition-all duration-200"
                  >
                    Download SD (480p)
                  </a>
                </div>
              </div>

              {/* Share */}
              <div className="bg-gray-50 rounded-lg p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Share This Episode</h3>
                <div className="flex space-x-3">
                  <button className="flex-1 bg-blue-600 text-white py-2 px-3 rounded-lg font-medium hover:bg-blue-700 transition-colors duration-200">
                    Facebook
                  </button>
                  <button className="flex-1 bg-blue-400 text-white py-2 px-3 rounded-lg font-medium hover:bg-blue-500 transition-colors duration-200">
                    Twitter
                  </button>
                  <button className="flex-1 bg-green-600 text-white py-2 px-3 rounded-lg font-medium hover:bg-green-700 transition-colors duration-200">
                    WhatsApp
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Related Episodes */}
      {relatedEpisodes.length > 0 && (
        <section className="py-12 bg-gray-50">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <h2 className="text-3xl font-bold text-gray-900 mb-8">More {episode.genre} Episodes</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {relatedEpisodes.map(relatedEpisode => (
                <div key={relatedEpisode.id} className="bg-white rounded-lg shadow-lg overflow-hidden hover:shadow-xl transition-shadow duration-200">
                  <img 
                    src={relatedEpisode.thumbnail}
                    alt={`Thumbnail for ${relatedEpisode.title}`}
                    className="w-full h-48 object-cover"
                  />
                  <div className="p-4">
                    <h3 className="font-semibold text-gray-900 mb-2">{relatedEpisode.title}</h3>
                    <p className="text-gray-600 text-sm mb-3 line-clamp-2">{relatedEpisode.description}</p>
                    <Link 
                      href={`/episodes/${relatedEpisode.id}`}
                      className="text-purple-600 hover:text-purple-700 font-medium text-sm"
                    >
                      Watch Now →
                    </Link>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
      )}

      <Footer />
    </div>
  );
}
