export function getEpisodeById(id: string): Episode | undefined {
  return episodes.find(episode => episode.id === id);
}

export function getEpisodesByGenre(genre: string): Episode[] {
  return episodes.filter(episode => episode.genre.toLowerCase() === genre.toLowerCase());
}

export function getFeaturedEpisodes(): Episode[] {
  return episodes.filter(episode => episode.rating >= 8.8);
}
